/* Navbar */

.navbar-1 {
  padding: 5px 20px;
  position: fixed;
  width: 100vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 10;
  p {
    margin: 0;
    font-size: 16px;
  }
  h3 {
    margin: 0;
    font-size: 18px;
    text-decoration: none;
  }
}


.nav-item {
  font-weight: bold;
  font-size: 14px;
  // text-transform: uppercase;
  text-decoration: none;
  color: $blue;
  padding: 20px 0px;
  margin: 0px 20px;
  display: inline-block;
  position: relative;
  opacity: 0.75;
}

.nav-item:hover {
  a {
    text-decoration: none;
    color: $blue;
    opacity: 1;
  }
}

.nav-item::before {
  transition: 300ms;
  height: 2px;
  content: "";
  position: absolute;
  background-color: $blue;
}

.nav-items-ltr::before {
  width: 0%;
  bottom: 15px;
}

.nav-items-ltr:hover::before {
  width: 60%;
}
