.button {
  color: $blue;
  width: 100%;
  margin: 10px;
  padding: 20px;
  border: 2px solid $blue;
  text-align: center;
  text-transform: uppercase;
  position: relative;
  overflow:hidden;
  transition: .3s;
  &:after {
    position: absolute;
    transition: .3s;
    content: '';
    width: 0;
    left: 50%;
    bottom: 0;
    height: 3px;
    background: $blue;
  }
  &.button:after {
    height: 120%;
    left: -10%;
    transform: skewX(15deg);
    z-index: -1;
  }
  &:hover {
    cursor: pointer;
    &:after {
      width: 100%;
      left: 0;
    }
    &.button {
      color: white;
    }
    &.button:after {
      left: -10%;
      width: 120%;
    }
  }
}

