// CONFIG

// Import Google fonts
@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,300,700|Raleway:400,100,300,700,500");
@import url('https://fonts.googleapis.com/css?family=Lora:400i,700i&display=swap');

// Define fonts for body and headers
$body-font: "Open Sans", "Helvetica", "sans-serif";
$headers-font: "Raleway", "Helvetica", "sans-serif";
$italic-font: "Lora", "serif";


// Colors
$red: #FE5F55;
$blue: #0FA3B1;
$yellow: #FFBA49;
$orange: #F77402;
$green: #5DEF62;
$gray: #3B413C;
$light-gray: #9DB5B2;
$background-color: #F7F7F7;


// Components import
@import "./components/_navbar.scss";
@import "./components/_footer.scss";
@import "./components/_banner.scss";
@import "./components/_buttons.scss";
@import "./components/_gallery.scss";
@import "./components/_web-cards-projects.scss";



/* Main */

body {
  margin: 0;
  background-color: $background-color;
}

a {
  text-decoration: none;
}

h1, h2 {
  font-family: $italic-font;
}

.container-main {
  height: 100vh;
}



/* Sections Titles */

.titles {
  padding: 30px 0;
}
