.container-web-projects {
  margin-bottom: 30px;
}

.card-columns {
  a{
    color: $gray;
  }
  a:hover {
    color: $gray;
    text-decoration: none;
  }
}

.web-card {
  margin-bottom: 25px;
  background-color: white;
  border-radius: 2px;
  box-shadow: 0 0 10px rgba(0,0,0,0.2);
  font-size: 18px;
  opacity: 0.9;
  display: inline-block;
  &:hover {
    opacity: 1;
  }
  h3 {
    font-size: 21px;
    font-weight: bold;
    margin: 8px 0;
  }
  p {
    font-size: 12px;
    line-height: 1.4;
    font-weight: 500em;
    opacity: .7;
    margin-bottom: 0;
  }
  img {
    // height: auto;
    width: 100%;
    object-fit: cover;
  }
  .info-web-card {
    // height: auto;
    padding: 20px;
    vertical-align: middle;
  }
}

.tags {
  color: $gray;
  font-size: 10px;
  font-family: $italic-font;
}

